#caro-spinner {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.07);

    > .caro-spinner-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50vh;
        width: 100vw;

        > div {
            left: -30px;
        }
    }
}
